import React,{useState} from 'react';
import {useHistory} from 'react-router-dom';

import { Divider,Image,Card,Modal} from 'antd';
import db from '../db.js'
import t from './t.js'

import {EventList} from './event.js'
import {SkillList} from './skill-detail.js'
// import RaceList from './player-race.js'
import {RaceSchedule,RaceTimeline} from '../components/race.js'
// import {EffectTable} from './effect.js'
const ua = db.get('ua').value();
const cdnServer = 'https://cdn.jsdelivr.net/gh/wrrwrr111/pretty-derby/public/'

const PlayerDetail = (props) =>{
  const id = props.playerId!==undefined?props.playerId:props.match.params.id
  //  
  const isNur = props.isNur!==undefined?props.isNur:parseInt(props.match.params.nur)
  const data = db.get('players').find({id}).value()
  const PlayerItem = () => <div style={{height:69,display:'flex'}}>
    <Image src={cdnServer+data.imgUrl} width={63} height={69} resizemode={'cover'}></Image>
    <div style={{display:'flex',height:64,flexDirection:'column',justifyContent:'space-between'}}>
      <div style={{fontSize:20,fontWeight:700}}>{data.name}</div>
      <div style={{fontSize:20,fontWeight:700,color:'gray'}}>{t(data.name)}</div>
      <div style={{fontSize:20,fontWeight:700}}>{data.charaName}</div>
      <div style={{fontSize:20,fontWeight:700,color:'gray'}}>{t(data.charaName)}</div>
    </div>
  </div>
  if(isNur){
    return <>
      <PlayerItem></PlayerItem>
      <Divider></Divider>
      <EventList eventList={data.eventList} pid={data.id} ></EventList>
      <Divider></Divider>
      {/* <RaceSchedule raceList={data.raceList}></RaceSchedule> */}
      <RaceTimeline raceList={data.raceList}></RaceTimeline>
      <Divider></Divider>
      <SkillList skillList={data.skillList}></SkillList>
    </>
  }else{
    return <>
    <PlayerItem></PlayerItem>
    <Divider>{t("")}</Divider>
    <AdaptBox player={data}></AdaptBox>
    <Divider>{t("")}</Divider>
    <GrowBox player={data}></GrowBox>
    <Divider>{t("")}</Divider>
    <SkillList skillList={data.skillList}></SkillList>
    <Divider>{t("")}</Divider>
    <EventList eventList={data.eventList} pid={data.id} ></EventList>
    <Divider>{t("")}</Divider>
    {/* <RaceSchedule raceList={data.raceList}></RaceSchedule> */}
    <RaceTimeline raceList={data.raceList}></RaceTimeline>
  </>
  }
}
const AdaptBox = (props)=>{

  const tableStyle = {
    width:"100%",
    cellPadding:4,
  }
  const cellStyle = {
    width:'20%',
    height:'32px',
    fontSize: 16,
    textAlign: 'flex-start',
    paddingLeft:16,
    fontWeight: 500,
    borderWidth:'thin',
    borderStyle:'none solid solid none',
    borderColor:'gray',
  }
  const headerCellStyle = {
    width:'20%',
    fontSize: 18,
    textAlign: 'center',
    fontWeight: 600,
    color:'#f5f5f5',
    textShadow: "0 2px #33333370",
    backgroundColor:'#32cd32C0',
    borderWidth:'thin',
    borderStyle:'none solid solid none',
    borderColor:'gray',
  }
  const adaptTextWrapperStyle = {paddingRight:16,display:'flex',width:'100%',alignItems:'center',justifyContent:'space-between'}

  const coloredGradeText = (text)=>{
    let color = 'gray';
    switch (text) {
      case 'S':
        color = '#FFD700';
        break
      case 'A':
        color = '#FFA500';
        break
      case 'B':
        color = '#BA55D3';
        break
      case 'C':
        color = '#90EE90';
        break
      case 'D':
        color = '#87CEEB';
        break
      default:
        color = 'gray'
    }
    return <div style={{fontSize:22,fontWeight:700,textShadow: "0 2px #33333370",color:color}}>{text}</div>
  }
  // {[`/\xa0`,coloredGradeText(props.player.grass)]}
  return(
    <div style={{borderRadius:'8px',borderStyle:'solid',borderWidth:'thin',borderColor:'gray'}}>
      <table {...tableStyle} >
        <tbody>
          <tr>
            <td style={{ ...headerCellStyle ,borderRadius:"8px 0 0 0",fontWeight:700,fontSize:18}}>{t("")}</td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.grass)]}</div></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.dirt)]}</div></td>
            <td style={{ ...cellStyle }}>{`\xa0`}</td>
            <td style={{ ...cellStyle ,borderRadius:"0 8px 0 0",borderStyle:'none none solid none'}}>{`\xa0`}</td>
          </tr>
          <tr>
            <td style={{ ...headerCellStyle ,fontWeight:700,fontSize:18}}>{t("")}</td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.shortDistance)]}</div></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.mile)]}</div></td>
            <td style={{ ...cellStyle }}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.mediumDistance)]}</div></td>
            <td style={{ ...cellStyle ,borderRadius:"0 0 8px 0",borderStyle:'none none solid none'}}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.longDistance)]}</div></td>
          </tr>
          <tr>
            <td style={{ ...headerCellStyle ,borderRadius:"0 0 0 8px",borderStyle:'none solid none none',fontWeight:700,fontSize:18}}>{t("")}</td>
            <td style={{ ...cellStyle ,borderStyle:'none solid none none'}}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.escape)]}</div></td>
            <td style={{ ...cellStyle ,borderStyle:'none solid none none'}}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.leading)]}</div></td>
            <td style={{ ...cellStyle ,borderStyle:'none solid none none'}}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.insert)]}</div></td>
            <td style={{ ...cellStyle ,borderRadius:"0 0 8px 0",borderStyle:'none none none none'}}><div style={adaptTextWrapperStyle}>{[`${t("")}`,coloredGradeText(props.player.tracking)]}</div></td>
          </tr>
        </tbody>
      </table>
    </div>
  )
}

const GrowBox= (props)=>{
  const tableStyle = {
    width:"100%",
    cellPadding:4,
  }
  const headerCellStyle = {
    width:'20%',
    fontSize: 18,
    textAlign: 'center',
    fontWeight: 600,
    color:'#f5f5f5',
    textShadow: "0 2px #33333370",
    backgroundColor:'#32cd32C0',
    borderWidth:'thin',
    borderStyle:'none solid solid none',
    borderColor:'gray',
  }
  const cellStyle = {
    width:'20%',
    height:'40px',
    fontSize: 16,
    textAlign: 'center',
    fontWeight: 500,
    borderWidth:'thin',
    borderStyle:'none solid none none',
    borderColor:'gray',
  }

  return(
    <div style={{borderRadius:'8px',borderStyle:'solid',borderWidth:'thin',borderColor:'gray'}}>
      <table {...tableStyle} >
        <tbody>
          <tr>
            <th style={{ ...headerCellStyle ,borderRadius:"8px 0 0 0"}}>{t("")}</th>
            <th style={{ ...headerCellStyle }}>{t("")}</th>
            <th style={{ ...headerCellStyle }}>{t("")}</th>
            <th style={{ ...headerCellStyle }}>{t("")}</th>
            <th style={{ ...headerCellStyle ,borderRadius:"0 8px 0 0",borderStyle:'none none solid none'}}>{t("")}</th>
          </tr>
          <tr>
            <td style={{ ...cellStyle ,borderRadius:"0 0 0 8px"}}>{props.player.speedGrow}</td>
            <td style={{ ...cellStyle }}>{props.player.staminaGrow}</td>
            <td style={{ ...cellStyle }}>{props.player.powerGrow}</td>
            <td style={{ ...cellStyle }}>{props.player.gutsGrow}</td>
            <td style={{ ...cellStyle ,borderRadius:"0 0 8px 0",borderStyle:'none none none none'}}>{props.player.wisdomGrow}</td>
          </tr>
        </tbody>
      </table>
    </div>
  )
}
const PlayerCard = (props)=>{
  const history = useHistory();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const player = props.data

  const showPlayerDetail = () => {
    if(props.onSelect){
      props.onSelect(player)
    }else{
      if(ua==='mo'){
        history.push(`/player-detail/${player.id}/0`)
      }else{
        setIsModalVisible(true);
      }
    }
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      <Card cover={
        <Image src={cdnServer+player.imgUrl} preview={false} onClick={showPlayerDetail} width={'100%'}></Image>

      }>
        <Card.Meta title={t(player.name)} ></Card.Meta>
        <Card.Meta title={t(player.charaName)} ></Card.Meta>
      </Card>
      <Modal visible={isModalVisible} onOk={handleOk} onCancel={handleCancel} footer={null} width={800} >
        <PlayerDetail playerId={player.id} isNur={false}></PlayerDetail>
      </Modal>
    </>
  )
}
export {PlayerDetail,PlayerCard}

